import React from 'react';

import {Prompt} from 'react-router-dom';

class ContactPage extends React.Component {
    state = {
        value: "",
    }    

    handleSubmit = (e) => {
        e.preventDefault() //eby si nie wysyao przez HTML
        this.setState({
            value: ""
        })
    }

    handleChange = (e) => {
        this.setState({
            value: e.target.value
        })
    }

    render() {
        return(
            <div className="contact">
                <form onSubmit={this.handleSubmit}> {/* eby si nie wysyao przez HTML*/}
                    <h3>Napisz do nas</h3>
                    <textarea value={this.state.value}
                      onChange={this.handleChange}
                      placeholder="Wpisz wiadomo..."></textarea>
                    <button>Wylij</button>
                </form>
                <Prompt /* Warunek sprawdzany, przy prubie zmiany strony. Gdy warunek jest speniony, blokuje zmian strony */
                    when={ this.state.value ? true : false }
                    message="W formularzu znajduje si nie wysana tre. Czy na pewno chesz opuci t stron"
                />
            </div>
            

        );
    }
}

export default ContactPage;