import React from 'react';

const ZbiorkaPage = () => {
    return ( 
        <>
        <h1>Zbirka rolek</h1>
            Nazwa akcji oraz pierwszy pomys jaki przyszed mi do gowy, to zoragnozwa zbirk nieurzywanego 
            juz sprztu rolkarskiegi i ponowne wykorzystanie go. 
            Zebrane rolki chciaem ofiarowa moim uczniom wycznie "za umiech".
            
            <br/>Moim celem byy rodowiska, gdzie rodzicw nie sta na kupno rolek dla swoich pociech, lub z rznych przyczyn,
            dzieci nie maj swoich rolek. 
            Druga grpa odbiorcw, to osoby, ktre nie wiedz czy ten rodzaj sprtu jest dla nich i obawiaj sie wyda znaczn 
            sum pieniedzy na sprzt, ktry by moz ezao tylko raz.
            
            <br/>Dzikuje wszystkim osob, ktre oday swj nieurzywany sprzt. Kilka osb oddao nowe, tylko raz urzywane, pikne rolki.
            Do akcji doczya si szkka rolkarska z Rzeszowa, ktra za "grosze" odsprzedaa kilk par rolek.

            <br/>Z uzbieranych dotychczas rolek, udao si zorganizowa mnstwo akcji, nauk, warsztaw.
            <br/>Nauczylimy jedzic dosownie setki osb na warsztatach grupowych oraz na lekcjach indywidualnych.

            Przez moje rce przewino si bardzo wiel par rolek, z czego wikszo przekazaem miedyz innymi:
            <ul>
                <li>osobom indywidualnym,</li>
                <li>dzieciom ze wietlic socjoterapeutycznych,</li>
                <li>dzieciom z domw dziecka,</li>
                <li>jedna para wyjechaa na Litw czyli akcja ma juz rozmiar globalny ;)  </li>
            </ul>

            <br/>Jeszcze raz dzikuej wszystkim, bez ktcyh ta kacja by si nie udaa.

            <br/>Obecnie z powodu mniejszej ilosci czasu oraz mniejszej ilosci miejsca w garau, ju nie prowadz zbirki rolek.

        </>
     );
}
 
export default ZbiorkaPage;